	module.exports = {"data":{"markdownRemark":{"html":"<p>早晨六点从旅店出发，乘座BUS到机场，一切顺利。乘座日本全日空NH919点到达上海。</p>\n<p>这次到日本访问，得出一个总的印象。由于中国社会经济的发展，日本各界目前看好中国，因而对中国重要性的认识有所加强。与过去相比，日本大众媒介对中国问题的报道积极性方面的东西增加。但是，总体上日本对中国是持一种复杂矛盾的心理的，一方面是看不起中国，也不希望中国发展起来，以避免形成实际上的竞争对手，特别是在亚太地区;另一方面日本也希望中国能够发展，这样中国就可以保持稳定，不致于发生动乱，这对日本也有利。在这种矛盾的心理状态下，日本对华的政策和对中国的态度就是矛盾的：在经济上，既给予中国一定的支持，以取得市场，但同时又不在高科技和国民经济发展的关键领域支持中国的发展;在政治上，一方面希望与中国保持较好的关系，另一方面又与西方国家结盟，保持对中国的压力\"。中国威胁论\"在日本有一定的市场。</p>\n<p>从目前的总的发展态势上来看，也由于中国显示的强劲的发展势头，日本希望与中国保持良好的关系，至少是和平的关系。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0093.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0093.md","frontmatter":{"title":"5月19日 星期四"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0093.md absPath of file >>> MarkdownRemark"}}

